import React from 'react'
import { useState, useEffect } from 'react'
import { requests } from '../apiLinks'
import axios from 'axios'
import './css/banner.css'

function Banner() {
    const [movieBanner, setMovieBanner] = useState([])
    const baseUrl = 'https://image.tmdb.org/t/p/w780'


    useEffect(() => {

        async function getPopularMovie() {
            const resp = await (axios.get(requests.popularMovies))
            setMovieBanner(resp.data.results[Math.floor(Math.random() * resp.data.results.length - 1)])
            return resp
        }
        getPopularMovie()

    }, [])

    return (
        <header className="banner"
            style={{
                backgroundImage: `url(https://image.tmdb.org/t/p/w780${movieBanner.backdrop_path})`,
                backgroundPosition: "center center",
                backgroundSize: "cover"
            }}
        >
            <div className="banner_contents">
                <h1>{movieBanner.title}</h1>
                <div className="banner_btn">
                    <button>Play</button>
                    <button>My List</button>

                </div>
                <p>{movieBanner.overview}</p>

            </div>
            <div className="banner_fade" />
        </header>

    )
}

export default Banner
