import React, { useState, useEffect } from 'react'
import './css/movie.css'
import axios from 'axios'
import Youtube from 'react-youtube'
import movieTrailer from 'movie-trailer'

function MovieRows({ title, fetchData, isLarge }) {

    const baseUrl = 'https://image.tmdb.org/t/p/w200'

    const opts = {
        height: '390',
        width: '100%',
        playerVars: {
            // https://developers.google.com/youtube/player_parameters
            autoplay: 1,
        },
    };

    const [movies, setMovies] = useState([])
    const [trailerUrl, setTrailerUrl] = useState('')

    const handleClick = (ele) => {
        if (trailerUrl) {
            setTrailerUrl('')
        }
        else {
            movieTrailer(ele.original_title)
                .then(url => {
                    // setTrailerUrl(url)
                    const urlParams = new URLSearchParams(new URL(url).search)
                    setTrailerUrl(urlParams.get('v'))
                })

        }
    }

    useEffect(() => {
        async function getMovies() {
            const response = await (axios.get(fetchData))
            // console.log(response.data.results)
            setMovies(response.data.results)
            return response

        }
        getMovies()
    }, [fetchData])


    return (
        <div className="container">
            <h1 className="title">{title}</h1>
            <div className="movieslist">
                {movies.map(ele =>

                    (<img className={`movie_poster ${isLarge ? "isLarge" : ""}`} src={isLarge ? `${baseUrl}${ele.poster_path}` : `${baseUrl}${ele.backdrop_path}`} key={ele.id} onClick={() => handleClick(ele)} alt="movies"></img>)
                )
                }
            </div>

            {trailerUrl && <Youtube videoId={trailerUrl} opts={opts} />}
        </div>
    )
}

export default MovieRows
